import {fetch_local, fetch_server} from '@/config/fetch'

/**
 * 登陆
 */

export const login = data => fetch_server('/account/login', data, 'POST');

/**
 * 退出
 */
export const logout = () => fetch_server('/account/logout');

/**
 * 修改密码
 */
export const updPwd = data => fetch_server("/account/updatePwd", data, 'PUT');


/**
 * 新增用户
 */
export const addUser = data => fetch_server("/user/addUser", data, 'POST');

/**
 * 更新用户
 */
export const updUser = data => fetch_server("/user/updUser", data, 'PUT');

/**
 * 查询用户
 */

export const selUser = data => fetch_server('/user/getUser/' + data);

/**
 * 模糊查询用户
 */

export const selFuzzyUser = (account, name, page) => fetch_server('/user/FuzzySearchUser?account=' + account + '&name=' + name + '&page=' + page);

/**
 * 添加奖项
 */
export const addReward = data => fetch_server("/creditOption/add", data, 'POST');

/**
 *奖项列表
 */
export const rewardList = (data) => fetch_server("/creditOption/getAllOptions?page=" + data)

/**
 *查询奖项类别
 */
export const getCategory = () => fetch_server("/creditOption/getCategory")

/**
 *查询奖项
 */
export const getOptions = (category, rank, page) => fetch_server("/creditOption/getOptions?category=" + category + "&rank=" + rank + "&page=" + page)

/**
 * 更新奖项
 */
export const updReward = data => fetch_server("/creditOption/update", data, 'PUT');

/**
 * 删除奖项
 */
export const delReward = data => fetch_server("/creditOption/delete", data, 'DELETE');


/**
 * 通过奖项Id获取此项学分
 */
export const getScoreByCreditId = (creditId) => fetch_server("/creditOption/getScoreByCreditId?creditId=" + creditId);

/**
 * 获取工单列表
 */
export const getWorkOrderList = (account, status, page) => fetch_server("/workOrder/getWorkOrderList?account=" + account + "&status=" + status + "&page=" + page);

/**
 * 获得待审核工单数
 */
//export const getOrderPendingSum = () => fetch_server("/workOrder/getOrderSumByPending")

/**
 * 通过申请
 */
export const passWorkOrder = data => fetch_server("/workOrder/passWorkOrder", data, 'POST');

/**
 * 驳回申请
 */
export const rejectWorkOrder = data => fetch_server("/workOrder/rejectWorkOrder", data, 'POST');

/**
 * 获取(权限范围内)class
 */
export const getClasses = () => fetch_server("/user/getClasses");

/**
 *学分逆序列表
 */
export const scoreList = (page) => fetch_server("/user/getScoreList?page=" + page)

/**
 * 获取指定班级学生信息
 */
export const getUserByCondition = (college, profession, classroom, page) => fetch_server("/user/getUserByCondition?college=" + college + "&profession=" + profession + "&classroom=" + classroom + "&page=" + page);

/**
 * 根据学生账号获取学生信息
 */
export const getStuByAccount = (account, page) => fetch_server("/user/getStuByAccount?account=" + account + "&page=" + page);



