<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">
                <el-form-item label="类别">
                    <el-input v-model="sizeForm.category"></el-input>
                </el-form-item>
                <el-form-item label="内容">
                    <el-input v-model="sizeForm.rank"></el-input>
                </el-form-item>
                <el-form-item label="学分">
                    <el-input v-model="sizeForm.score"></el-input>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="primary" @click="onSubmit">立即提交</el-button>
                    <el-button @click="onReset">重置</el-button>
                </el-form-item>
            </el-form>

        </div>
    </div>
</template>

<script>
    import {addReward} from '@/api/getData'

    export default {
        data() {
            return {
                sizeForm: {
                    category: '',
                    rank: '',
                    score: '',
                }
            };
        },
        methods: {
            async onSubmit() {
                const data = {

                    category: this.sizeForm.category,
                    rank: this.sizeForm.rank,
                    score: this.sizeForm.score,
                }
                const res = await addReward(data)
                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '添加成功'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: '添加失败 :' + res.msg
                    });
                }

            }, onReset() {

                this.sizeForm.category = '';
                this.sizeForm.rank = '';
                this.sizeForm.score = '';
            }
        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        height: 50px;
        width: 50%;
    }

    .table_container {
        padding: 20px;
    }
</style>




