<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="sizeForm" class="demo-form-inline">
                <el-form-item label="账号">
                    <el-input v-model="sizeForm.account" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onQueryAll">全部</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="warning" @click="onQueryIng">待审核</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" @click="onQueryPass">已通过</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="onQueryReject">已驳回</el-button>
                </el-form-item>
            </el-form>
            <el-table
                    :data="tableData5"
                    border
                    style="width: 100%">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="说明">
                                <span>{{ props.row.msg }}</span>
                            </el-form-item>
                            <el-form-item label="审核人">
                                <span>{{ props.row.authorName }}</span>
                            </el-form-item>
                            <el-form-item label="证明">
                                <img :src="props.row.url" width="138" height="138"/>
                                <a href="javascript:void(0)" @click="onReview(props.row.url)"><span class="primary">预览</span></a>
                            </el-form-item>
                            <el-form-item label="回复">
                                <span>{{ props.row.reply }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="工单ID"
                        min-width="133">
                </el-table-column>
                <el-table-column
                        prop="account"
                        label="账号"
                        min-width="120">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="姓名"
                        min-width="100">
                </el-table-column>
                <el-table-column
                        prop="category"
                        label="奖项类别"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="rank"
                        label="奖项内容"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label="发起时间"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="endTime"
                        label="结束时间"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="status"
                        label="状态"
                        min-width="80">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status== 1" style="color:#f7ba2a">待审核</span>
                        <span v-if="scope.row.status== 2" style="color:#42d885">已通过</span>
                        <span v-if="scope.row.status== 3" style="color:#ff4949">已驳回</span>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1"
                    :page-size="10"
                    layout="total, prev, pager, next"
                    :total="rows">
            </el-pagination>
        </div>
        <el-dialog
                :visible.sync="centerDialogVisible"
                :modal-append-to-body="false"
                width="30%"
                center>
            <span slot="footer" class="dialog-footer">
                <img :src="imgUrl" width="500" height="500"/>
    <el-button @click="centerDialogVisible = false">关闭</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    import {getWorkOrderList} from '@/api/getData'

    export default {
        data() {
            return {

                sizeForm: {
                    account: ''

                },
                tableData5: [],
                status: 0,
                currentPage1: 1,
                rows: 0,
                centerDialogVisible: false,
                imgUrl: "",
            };
        },
        methods: {
            async onQueryAll() {
                this.status = 0;
                await this.getWorkOrder(1);
            }, async onQueryIng() {
                this.status = 1;
                await this.getWorkOrder(1);
            }, async onQueryPass() {
                this.status = 2;
                await this.getWorkOrder(1);
            }, async onQueryReject() {
                this.status = 3;
                await this.getWorkOrder(1);
            }, async handleCurrentChange() {
                await this.getWorkOrder(this.currentPage1);

            }, onReview(url) {
                if (url == '') {
                    this.$message({
                        type: 'error',
                        message: '图片地址不存在'
                    });
                    return;
                }
                this.centerDialogVisible = true;
                this.imgUrl = url;
            },
            async getWorkOrder(page) {

                const users = await getWorkOrderList(this.sizeForm.account, this.status, page)
                if (users.code == '0200') {

                    /* for (var i = 0; i < users.data.length; i++) {
                         console.log(users.data[i].status == 1)
                         if (users.data[i].status == 1) {
                             users.data[i].status = '待审核';
                         } else if (users.data[i].status == 2) {
                             users.data[i].status = '已通过';
                         } else if (users.data[i].status == 3) {
                             users.data[i].status = '已驳回';
                         }
                     }*/
                    this.tableData5 = users.data;
                    this.rows = users.data[0].rows;


                } else {
                    this.$message({
                        type: 'error',
                        message: users.msg
                    });
                }

            }
        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /*.el-form-item {
        margin-right: 0;
        margin-bottom: 0;

    }*/
    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

    .table_container {
        padding: 20px;
    }
</style>




