<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="sizeForm" class="demo-form-inline" >
                <el-form-item label="账号">
                    <el-input v-model="sizeForm.account" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button class="query" type="primary" @click="onQuery">查询</el-button>
                </el-form-item>
            </el-form>
            <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item class="ac" label="账号" prop="account">
                    <el-input v-model="ruleForm.account"></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="学院" prop="college">
                    <el-input v-model="ruleForm.college"></el-input>
                </el-form-item>
                <el-form-item label="专业" prop="profession">
                    <el-input v-model="ruleForm.profession"></el-input>
                </el-form-item>
                <el-form-item label="班级" prop="classroom">
                    <el-input v-model="ruleForm.classroom"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="classroom">
                    <el-input v-model="ruleForm.email"></el-input>
                </el-form-item>
                <el-form-item label="身份" prop="level">
                    <el-select v-model="value" placeholder="请选择用户身份">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onUpdate">立即更新</el-button>
                </el-form-item>
            </el-form>

        </div>
    </div>
</template>

<script>
    import {updUser, selUser} from '@/api/getData'

    export default {
        data() {
            return {
                options: [{
                    value: '1',
                    label: '学生'
                }, {

                    value: '2',
                    label: '教师',
                }, {
                    value: '3',
                    label: '专业领导',
                }, {
                    value: '4',
                    label: '学院领导',
                }, {
                    value: '5',
                    label: '审核人员'
                }, {
                    value: '9',
                    label: '管理员'
                }
                ],
                value: '',
                sizeForm: {
                    account: '',
                }, ruleForm: {
                    id: '',
                    account: '',
                    name: '',
                    college: '',
                    profession: '',
                    classroom: '',
                    email: '',
                    level: '',
                }
            };
        },
        methods: {
            async onQuery() {

                const user = await selUser(this.sizeForm.account)
                if (user.code == '0200') {

                    this.ruleForm.id = user.data.id;
                    this.ruleForm.account = user.data.account;
                    this.ruleForm.name = user.data.name;
                    this.ruleForm.college = user.data.college;
                    this.ruleForm.profession = user.data.profession;
                    this.ruleForm.classroom = user.data.classroom;
                    this.ruleForm.email = user.data.email;
                    this.value = '' + user.data.level;

                } else {
                    this.$message({
                        type: 'error',
                        message: user.msg
                    });
                }

            }, async onUpdate() {

                const data = {
                    email: this.ruleForm.email,
                    id: this.ruleForm.id,
                    account: this.ruleForm.account,
                    name: this.ruleForm.name,
                    college: this.ruleForm.college,
                    profession: this.ruleForm.profession,
                    classroom: this.ruleForm.classroom,

                    level: this.value
                }
                const res = await updUser(data)

                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '更新成功'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }
            }
        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    .demo-ruleForm .el-form-item {
        margin-right: 30px;
        margin-bottom: 0;
        height: 50px;
        width: 49%;
    }
	.demo-form-inline{
	
	margin-left:60px;
	}
	/*.query{
	  position:absolute;
	  right:300px;
	}*/
    .table_container {
        padding: 20px;
    }
</style>




