/**
 * 配置编译环境和线上环境之间的切换
 *
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * baseImgPath: 图片存放地址
 *
 */
let baseUrl = '';
let routerMode = 'history';
let baseImgPath = 'http://images.cangdu.org/';
let serverUrl = "http://api.wanpeng123.cn";

if (process.env.NODE_ENV == 'development') {
	//baseUrl = 'http://cangdu.org:8001';
}else{
	//baseUrl = 'http://cangdu.org:8001';
	baseUrl = '127.0.0.1:8080';
    //baseUrl = 'http://cangdu.org:8001';
}

export {
    baseUrl,
    routerMode,
    baseImgPath,
    serverUrl
}



