<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">
                <el-form-item label="旧密码">
                    <el-input v-model="sizeForm.pwd"></el-input>
                </el-form-item>
                <el-form-item label="新密码">
                    <el-input v-model="sizeForm.newPwd"></el-input>
                </el-form-item>
                <el-form-item label="重复">
                    <el-input v-model="sizeForm.reNewPwd"></el-input>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="primary" @click="onSubmit">立即修改</el-button>
                    <el-button @click="onReset">重置</el-button>
                </el-form-item>
            </el-form>

        </div>
    </div>
</template>

<script>
    import {updPwd} from '@/api/getData'

    export default {
        data() {
            return {
                sizeForm: {
                    pwd: '',
                    newPwd: '',
                    reNewPwd: '',
                }
            };
        },
        methods: {
            async onSubmit() {

                if (this.sizeForm.newPwd == '') {
                    this.$message({

                        type: 'error',
                        message: '密码不能为空'
                    })
                    return;
                }
                if (this.sizeForm.newPwd != this.sizeForm.reNewPwd) {
                    this.$message({

                        type: 'error',
                        message: '两次密码不一致'
                    })
                    return;
                }
                if (this.sizeForm.pwd == this.sizeForm.newPwd) {
                    this.$message({

                        type: 'error',
                        message: '新旧密码不能相同'
                    })
                    return;
                }
                const data = {
                    pwd: this.sizeForm.pwd,
                    newPwd: this.sizeForm.newPwd,
                }
                const res = await updPwd(data)
                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '更新成功'
                    });
                    sessionStorage.clear();
                    this.$router.push({path: '/'})
                } else {
                    this.$message({
                        type: 'error',
                        message: '更新失败 :' + res.msg
                    });
                }

            }, onReset() {

                this.sizeForm.pwd = '';
                this.sizeForm.newPwd = '';
                this.sizeForm.reNewPwd = '';
            }
        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        height: 50px;
        width: 50%;
    }

    .table_container {
        padding: 20px;
    }
</style>




