import {baseUrl, serverUrl} from './env'

async function fetch_data(url = '', data = {}, type = 'GET', method = 'fetch', headers = {}) {
    type = type.toUpperCase();
    if (type == 'GET') {
        let dataStr = ''; //数据拼接字符串
        Object.keys(data).forEach(key => {
            dataStr += key + '=' + data[key] + '&';
        })

        if (dataStr !== '') {
            dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'));
            url = url + '?' + dataStr;
        }
    }

    if (window.fetch && method == 'fetch') {
        let requestConfig = {
            credentials: 'include',
            method: type,
            headers: {
                'Accept': 'application/json',
                /*'Content-Type': 'application/json'*/
            },
            mode: "cors",
        }
        if (headers != null) {
            Object.assign(requestConfig.headers, headers);
        }
        if (type == 'POST' || type == 'PUT' || type == 'DELETE') {
            var _data = new FormData();
            for (var key in data) {
                _data.append(key, data[key]);
            }

            Object.defineProperty(requestConfig, 'body', {
                value: _data
            })


        }

        try {

            const response = await fetch(url, requestConfig);
            const responseJson = await response.json();
            if (url.indexOf('login') != -1) {
                if (responseJson.code == '0200') {
                    sessionStorage.setItem("token", responseJson.data.token);
                    sessionStorage.setItem("data", JSON.stringify(responseJson.data));
                }
            }
            if (url.indexOf("signout") != -1) {

                location.href = "/";
                return;
            }

            if (responseJson.code == '0404') {
                alert('身份过期,请重新登陆!');

                location.href = "/";
                return;
            }
            if (responseJson.code == '0402') {
                sessionStorage.clear();
                location.href = "/";
                return ;
            }
            return responseJson
        } catch (error) {
            throw new Error(error)
        }
    } else {
        return new Promise((resolve, reject) => {
            let requestObj;
            if (window.XMLHttpRequest) {
                requestObj = new XMLHttpRequest();
            } else {
                requestObj = new ActiveXObject;
            }

            let sendData = '';
            if (type == 'POST' || type == 'PUT') {
                sendData = JSON.stringify(data);
            }

            requestObj.open(type, url, true);
            requestObj.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
            requestObj.send(sendData);

            requestObj.onreadystatechange = () => {
                if (requestObj.readyState == 4) {
                    if (requestObj.status == 200) {
                        let obj = requestObj.response
                        if (typeof obj !== 'object') {
                            obj = JSON.parse(obj);
                        }
                        resolve(obj)
                    } else {
                        reject(requestObj)
                    }
                }
            }
        })
    }
}

function fetch_local(url = '', data = {}, type = 'GET', method = 'fetch', headers = null) {

    url = baseUrl + url;
    return fetch_data(url, data, type, method, headers);
}

function fetch_server(url = '', data = {}, type = 'GET', method = 'fetch', headers = null) {
    url = serverUrl + url;
    headers = {
        "token": sessionStorage.getItem('token')
    }
    return fetch_data(url, data, type, method, headers);
}


export {
    fetch_local,
    fetch_server
}



