<script src="../../api/getData.js"></script>
<template lang="pug">
#headerNav
    .headerNav--logo
        h3 {{ logoTitle }}
    .headerNav--menu
        el-menu.el-menu-demo( theme="dark" v-bind:default-active="activeIndex" mode="horizontal" @select="handleSelect" )
            // 邮件
           
            // 消息
            <!--el-submenu( index="2" )
                template( slot="title" )
                    el-badge.item( v-bind:value="0" v-bind:max="99" )
                        el-button( size="small" )
                    i.material-icons notifications-->

            // 个人信息
            el-submenu( index="3" )
                template(slot="title" ) {{username}}
                el-menu-item( index="3-2" ) 修改密码
                el-menu-item( index="3-3" ) 安全退出



</template>
<script>
    import {logout} from '@/api/getData'

    export default {

        data() {
            return {
                logoTitle: '后台管理系统',
                activeIndex: '1',
                name: '管理员',
                username: this.name,
            }
        },
        beforeCreate: async function () {

            let user = JSON.parse(sessionStorage.getItem("data"));
            if (user != null) {

                if (user.name != null) {
                    this.name = user.name;
                } else {
                    alert("账户非法,请重新登陆");
                    sessionStorage.clear();
                    location.href = "/";
                }
            } else {
                location.href = "/";
            }

          /*const res =  await getOrderPendingSum();
            if (res.code == '0200'){

                this.message = res.data;
            }else {
                 this.$message({
                        type: 'error',
                        message: '获取待审核订单数失败'
                    });
            }*/

        },
        methods: {
            handleSelect(key, keyPath) {

                if (key == '3-2') {
                    sessionStorage.setItem("router[0]", "个人设置");
                    sessionStorage.setItem("router[1]", "修改密码");
                    this.$data.breadcrumbLevelArr = this.$store.state.viewState.breadcrumbLevel;
                    this.$data.breadcrumbLevelArr[0].levelName = sessionStorage.getItem("router[0]");
                    this.$data.breadcrumbLevelArr[1].levelName = sessionStorage.getItem("router[1]");
                    this.$router.push({path: 'UpdPwd'})
                }
                if (key == '3-3') {

                    logout();
                    sessionStorage.clear();
                    location.href = "/";
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    @import '../../sass/main'

    #headerNav
        position: fixed
        z-index: 2
        +W100
        .headerNav--logo
            +floatL
            +flexCenter
            +w-h($D-sidebarWidth, $D-headerNavHeight)
            +bC($C-theme)
            > h3
                +fS($F-info)
                color: $F
        .headerNav--menu
            +mL($D-sidebarWidth)
            min-width: 760px
            height: $D-headerNavHeight
            .el-menu
                display: flex
                justify-content: flex-end

    // 下拉菜单 - 图标大小样式
    i.material-icons
        +fS($F-title*1.5)
        +pT(5px)
</style>



