<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-table
                    :data="tableData5"
                    border
                    style="width: 100%">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="说明">
                                <span>{{ props.row.msg }}</span>
                            </el-form-item>
                            <el-form-item label="审核人">
                                <span>{{ props.row.authorName }}</span>
                            </el-form-item>
                            <el-form-item label="证明">
                                <img :src="props.row.url" width="138" height="138"/>
                                <a href="javascript:void(0)" @click="onReview(props.row.url)"><span class="primary">预览</span></a>
                            </el-form-item>
                            <el-form-item label="回复">
                                <span>{{ props.row.reply }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="工单ID"
                        min-width="133">
                </el-table-column>
                <el-table-column
                        prop="creditId"
                        label="奖项ID"
                        v-if="false">
                </el-table-column>
                <el-table-column
                        prop="account"
                        label="账号"
                        min-width="120">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="姓名"
                        min-width="100">
                </el-table-column>
                <el-table-column
                        prop="category"
                        label="奖项类别"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="rank"
                        label="奖项内容"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label="发起时间"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="status"
                        label="状态"
                        min-width="80">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status== 1" style="color:#f7ba2a">待审核</span>
                        <span v-if="scope.row.status== 2" style="color:#42d885">已通过</span>
                        <span v-if="scope.row.status== 3" style="color:#ff4949">已驳回</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="operation"
                        label="操作"
                        width="180">
                    <template slot-scope="scope">
                        <el-button type="success" @click="onSuccess(scope.$index,scope.row)">通过</el-button>
                        <el-button type="danger" @click="onReject(scope.row)">驳回</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1"
                    :page-size="10"
                    layout="total, prev, pager, next"
                    :total="rows">
            </el-pagination>
        </div>
        <el-dialog
                :visible.sync="centerDialogVisible"
                :modal-append-to-body="false"
                width="30%"
                center>
            <span slot="footer" class="dialog-footer">
                <img :src="imgUrl" width="500" height="500"/>
    <el-button @click="centerDialogVisible = false">关闭</el-button>
  </span>
        </el-dialog>
        <el-dialog title="通过工单申请" :visible.sync="dialogFormVisible" :modal-append-to-body="false">
            <el-form :model="form">
                <el-form-item label="ID" :label-width="formLabelWidth">
                    <el-input v-model="form.id" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="学号" :label-width="formLabelWidth">
                    <el-input v-model="form.account" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="姓名" :label-width="formLabelWidth">
                    <el-input v-model="form.name" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item v-if="false">
                    <el-input v-model="form.creditId" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="学分" :label-width="formLabelWidth">
                    <el-input v-model="form.score" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="额外学分" :label-width="formLabelWidth">
                    <el-input v-model="form.otherScore" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="onTrue()">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="驳回工单申请" :visible.sync="dialogFormVisible1" :modal-append-to-body="false">
            <el-form :model="form1">
                <el-form-item label="ID" :label-width="formLabelWidth">
                    <el-input v-model="form1.id" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="回复" :label-width="formLabelWidth">
                    <el-input v-model="form1.msg" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="onFalse()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {getWorkOrderList, getScoreByCreditId, passWorkOrder, rejectWorkOrder} from '@/api/getData'

    export default {
        data() {
            return {
                tableData5: [],
                status: 0,
                currentPage1: 1,
                rows: 0,
                centerDialogVisible: false,
                imgUrl: "",
                dialogFormVisible: false,
                dialogFormVisible1: false,
                indexColum: '',
                form: {
                    account: '',
                    name: '',
                    creditId: '',
                    score: '',
                    otherScore: '',
                },
                form1: {

                    id: '',
                    msg: ''
                },
                formLabelWidth: '80px'
            };
        },
        beforeCreate: async function () {
            const res = await getWorkOrderList("", 1, 1);
            if (res.code == '0200') {
                this.tableData5 = res.data;
                this.rows = res.data[0].rows;
            } else {
                this.$message({
                    type: 'error',
                    message: res.msg
                });
            }
        },
        methods: {
            deleteRow(index, rows) {
                rows.splice(index, 1);
            },
            async handleCurrentChange() {
                await this.getWorkOrder(this.currentPage1);

            },
            async onSuccess(index, row) {
                const res = await getScoreByCreditId(row.creditId);
                this.form.id = row.id;
                this.form.account = row.account;
                this.form.name = row.name;
                this.form.score = res.data;
                this.form.creditId = row.creditId;
                this.dialogFormVisible = true;
                this.indexColum = index;
            }, async onTrue() {
                this.dialogFormVisible = false;
                const data = {

                    id: this.form.id,
                    otherScore: this.form.otherScore
                }
                const res = await passWorkOrder(data);

                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '已通过该申请'
                    });
                    this.deleteRow(this.indexColum, this.tableData5)
                    this.rows = this.rows - 1;
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }

            },
            async onReject(row) {

                this.form1.id = row.id;
                this.dialogFormVisible1 = true;
            }, async onFalse() {
                this.dialogFormVisible1 = false;
                const data = {
                    id: this.form1.id,
                    msg: this.form1.msg
                }
                const res = await rejectWorkOrder(data);

                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '已驳回该申请'
                    });
                    this.deleteRow(this.indexColum, this.tableData5)
                    this.rows = this.rows - 1;
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }
            },
            onReview(url) {
                if (url == '') {
                    this.$message({
                        type: 'error',
                        message: '图片地址不存在'
                    });
                    return;
                }
                this.centerDialogVisible = true;
                this.imgUrl = url;
            },
            async getWorkOrder(page) {

                const users = await getWorkOrderList("", 1, page)
                if (users.code == '0200') {
                    this.tableData5 = users.data;
                    this.rows = users.data[0].rows;


                } else {
                    this.$message({
                        type: 'error',
                        message: users.msg
                    });
                }

            }
        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /*.el-form-item {
        margin-right: 0;
        margin-bottom: 0;

    }*/
    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

    .table_container {
        padding: 20px;
    }
</style>




