<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="学号">
                    <el-input v-model="formInline.account" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onQuery">查询</el-button>
                </el-form-item>
            </el-form>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="ID"
                        min-width="140">
                </el-table-column>
                <el-table-column
                        prop="account"
                        label="学号"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="姓名"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="college"
                        label="学院"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="profession"
                        label="专业"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="classroom"
                        label="班级"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="score"
                        label="学分"
                        min-width="140">
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage1"
                        :page-size="10"
                        layout="total, prev, pager, next"
                        :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import {getStuByAccount} from '@/api/getData'

    export default {
        data() {
            return {

                formInline: {
                    account:''
                },
                tableData: [],
                options: [],
                total: 0,
                currentPage1: 1,
            }
        }
        ,
        methods: {
            async onQuery() {
                await this.getData(1);
            }
            ,
            async handleCurrentChange() {
                await this.getData(this.currentPage1);
            }
            ,
            async getData(page) {

                let account = this.formInline.account;
                const user = await getStuByAccount(account, page)
                if (user.code == '0200') {

                    this.tableData = user.data;
                    this.total = user.data[0].rows;
                } else {
                    this.$message({
                        type: 'error',
                        message: user.msg
                    });
                }
            }
            ,
            handleClick(row) {

                this.dialogFormVisible = true;
                this.form.id = row.id;
                this.form.category = row.category;
                this.form.rank = row.rank;
                this.form.score = row.score;

            }
            ,
            async onUpdate() {
                this.dialogFormVisible = false;
                const data = this.form;
                const res = await updReward(data);
                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '更新成功'
                    });
                    await this.getData(this.currentPage1);
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }
            }
            ,
            async onDelete(row) {

                this.$confirm('确认删除？')
                    .then(async _ => {
                        const res = await delReward({'id': row.id});
                        if (res.code == '0200') {
                            this.$message({
                                type: 'success',
                                message: '已删除'
                            });
                            await this.getData(this.currentPage1);
                        } else {
                            this.$message({
                                type: 'error',
                                message: res.msg
                            });
                        }
                    })
                    .catch(_ => {

                    });
            }
        }
    }
    ;
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /*.el-form-item {
        margin-right: 30px;
        margin-bottom: 0;
        height: 50px;
        width: 49%;
    }*/

    .query {
        position: absolute;
        right: 300px;
    }

    .table_container {
        padding: 20px;
    }

    .Pagination {
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
</style>




