import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login'
import Home from '@/views/Home'
//其他设置
import UpdPwd from '@/pages/Other/UpdPwd'  //其他设置-修改密码
//用户管理
import AddUser from '@/pages/User/AddUser'                    // 用户管理 - 添加用户
import UpdUser from '@/pages/User/UpdUser'                    // 用户管理 - 更新用户
import SelUser from '@/pages/User/SelUser'                    // 用户管理 - 查询用户
//奖项管理
import RewardList from '@/pages/Reward/RewardList'               // 奖项管理 - 奖项列表
import AddReward from '@/pages/Reward/AddReward'                // 奖项管理 - 新增奖项
import UpdReward from '@/pages/Reward/UpdReward'                // 奖项管理 - 更新奖项
//工单管理
import WorkOrderList from '@/pages/WorkOrder/WorkOrderList'         // 工单管理 - 工单列表
import Pending from '@/pages/WorkOrder/Pending'               // 工单管理 - 待审核
//学分管理
import DescList from '@/pages/Score/DescList'                  // 学分管理 - 逆序列表
import SelByClass from '@/pages/Score/SelByClass'                // 学分管理 - 按条件查询
import SelByNum from '@/pages/Score/SelByNum'                  // 学分管理 - 按班级查询

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            component: Login
        },
        {
            path: '/Home',
            name: '',
            component: Home,
            children: [
                {
                    path: '/',
                    components: {
                        ContainerView: AddUser
                    }
                },
                // 用户管理
                {
                    path: '/AddUser',
                    components: {
                        ContainerView: AddUser
                    }
                }, {
                    path: '/UpdUser',
                    components: {
                        ContainerView: UpdUser
                    }
                }, {
                    path: '/SelUser',
                    components: {
                        ContainerView: SelUser
                    }
                },
                // 奖项管理
                {
                    path: '/RewardList',
                    components: {
                        ContainerView: RewardList
                    }
                }, {
                    path: '/AddReward',
                    components: {
                        ContainerView: AddReward
                    }
                }, {
                    path: '/UpdReward',
                    components: {
                        ContainerView: UpdReward
                    }
                },
                // 工单管理
                {
                    path: '/WorkOrderList',
                    components: {
                        ContainerView: WorkOrderList
                    }
                }, {
                    path: '/Pending',
                    components: {
                        ContainerView: Pending
                    }
                },
                // 学分管理
                {
                    path: '/DescList',
                    components: {
                        ContainerView: DescList
                    }
                }, {
                    path: '/SelByClass',
                    components: {
                        ContainerView: SelByClass
                    }
                }, {
                    path: '/SelByNum',
                    components: {
                        ContainerView: SelByNum
                    }
                },
				{
                    path: '/UpdPwd',
                    components: {
                        ContainerView: UpdPwd
                    }

                }
            ]
        }
    ]
})



