<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="ID"
                        min-width="120">
                </el-table-column>
                <el-table-column
                        prop="category"
                        label="类别"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="rank"
                        label="内容"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="score"
                        label="学分"
                        min-width="130">
                </el-table-column>
                <el-table-column
                        prop="createName"
                        label="创建人"
                        min-width="150">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label="创建时间"
                        min-width="220">
                </el-table-column>
                <el-table-column
                        prop="updateTime"
                        label="更新时间"
                        min-width="220">
                </el-table-column>
            </el-table>
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1"
                    :page-size="10"
                    layout="total, prev, pager, next"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {rewardList} from '@/api/getData'

    export default {
        data() {
            return {
                tableData: [],
                total: 0,
                currentPage1: 1,
            }
        },
        beforeCreate: async function () {

            const res = await rewardList(1);
            if (res.code == '0200') {

                console.log(res)
                this.tableData = res.data;
                this.total = res.data[0].rows;

            } else {
                this.$message({
                    type: 'error',
                    message: res.msg
                });
            }
        },
        methods: {
            async handleCurrentChange() {

                await this.getRewardList(this.currentPage1);
            },
            async getRewardList(page) {
                const res = await rewardList(page);
                if (res.code == '0200') {

                    console.log(res)
                    this.tableData = res.data;
                    this.total = res.data[0].rows;

                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }

            }


        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    .el-form-item {
        margin-right: 30px;
        margin-bottom: 0;
        height: 50px;
        width: 49%;
    }

    .query {
        position: absolute;
        right: 300px;
    }

    .table_container {
        padding: 20px;
    }
</style>




