<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="sizeForm" class="demo-form-inline">
                <el-form-item label="账号">
                    <el-input v-model="sizeForm.account" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="sizeForm.name" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onQuery">查询</el-button>
                </el-form-item>
            </el-form>

            <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="ID"
                        min-width="80">
                </el-table-column>
                <el-table-column
                        prop="account"
                        label="账号"
                        min-width="120">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="姓名"
                        min-width="100">
                </el-table-column>
                <el-table-column
                        prop="college"
                        label="学院"
                        min-width="150">
                </el-table-column>
                <el-table-column
                        prop="profession"
                        label="专业"
                        min-width="120">
                </el-table-column>
                <el-table-column
                        prop="classroom"
                        label="班级"
                        min-width="80">
                </el-table-column>
                <el-table-column
                        prop="score"
                        label="学分"
                        min-width="100">
                </el-table-column>
                <el-table-column
                        prop="level"
                        label="身份"
                        min-width="100">
                </el-table-column>
                <el-table-column
