                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="updateTime"
                        label="更新日期"
                        min-width="192">
                </el-table-column>
            </el-table>
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1"
                    :page-size="10"
                    layout="total, prev, pager, next"
                    :total="rows">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {selFuzzyUser} from '@/api/getData'

    export default {
        data() {
            return {

                sizeForm: {
                    account: '',
                    name: ''

                },
                tableData: [],
                currentPage1: 1,
                rows: 0,
            };
        },
        methods: {
            async onQuery() {
                await this.getFuzzyUser(1);
            }, async handleCurrentChange() {
                await this.getFuzzyUser(this.currentPage1);

            }, async getFuzzyUser(page) {

                const users = await selFuzzyUser(this.sizeForm.account, this.sizeForm.name, page)
                if (users.code == '0200') {

                    this.tableData = users.data;
                    this.rows = users.data[0].sum;

                } else {
                    this.$message({
                        type: 'error',
                        message: user.msg
                    });
                }

            }
        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /*.el-form-item {
        margin-right: 0;
        margin-bottom: 0;

    }*/

    .table_container {
        padding: 20px;
    }
</style>




